import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, ToastController} from 'ionic-angular';

@Component({
  selector: 'page-about',
  templateUrl: 'about.html'
})
export class AboutPage {
	username: any;
	password: any;
  constructor(public navCtrl: NavController, public LoadingCtrl: LoadingController, private ToastCtrl: ToastController) {
  	this.username = '';
  	this.password = '';
  }

  showFill() {
  	
  	if(this.username == '' || this.password == '')
  	{
  		let ToastUserName = this.ToastCtrl.create({
  			duration: 3000,
  			message: 'username and password should not be null',
  			position: 'top'
  		});

  		ToastUserName.present();
  	}else {
  		let loading = this.LoadingCtrl.create({
	  		content: "please wait...",
	  		spinner: 'dots',
	  		duration: 5000
	  	});

	  	loading.present();
  	}

  }

}



